import icon from '../../assets/icon.svg';
import { ReactComponent as Line } from '../../assets/line.svg';
import { ReactComponent as MenuOpen } from '../../assets/menu-open.svg';
import { ReactComponent as MenuClose } from '../../assets/menu-close.svg';


import { Link } from 'react-router';
import Navbar from './Navbar';
import { useState } from 'react';

function Header() {
    const [openMenu, setOpenMenu] = useState(false);

    return (
        <>
            <header className='pt-2.5 px-2.5 md:py-2.5 md:px-9 relative'>
                <div className='flex justify-between items-center flex-wrap'>
                    <Link to='/' className='flex items-center gap-3'>
                        <img className="w-8 h-8 md:w-16 md:h-16" src={icon} alt="Logo de mi portafolio" />
                        <h1 className='md:text-5xl text-2xl font-bold dark:text-base-light'>AlexTakao</h1>
                    </Link>
                    <Navbar openMenu={openMenu} setOpenMenu={setOpenMenu} />
                    <button className='md:hidden pb-2 px-1' onClick={() => { setOpenMenu(!openMenu) }}>
                        {openMenu ? <MenuClose className="w-8 h-8 dark:text-gray-light"/> : <MenuOpen className="w-8 h-8 dark:text-gray-light"/>}
                    </button>
                </div>
                <Line className="w-full" />
            </header>
        </>
    );
}

export default Header;