import { Link, useLocation } from "react-router";
import { NAV_MENUS } from "../../data/navMenu";
import { NavbarProps } from "../../types/nav";
import { useEffect, useRef } from "react";

function Navbar({ section = 'home', openMenu, setOpenMenu }: NavbarProps) {
    const location: String = useLocation().pathname;
    const routes = NAV_MENUS[section] ?? [];
    const menuRef = useRef<HTMLDivElement>(null);

    useEffect(() => {
        const handleClickOutside = (event: MouseEvent) => {
            if (menuRef.current && !menuRef.current.contains(event.target as Node)) {
                setOpenMenu(false);
            }
        };

        document.addEventListener("mousedown", handleClickOutside);
        return () => {
            document.removeEventListener("mousedown", handleClickOutside);
        };
    }, [setOpenMenu]);

    return (
        <>
            <nav className="hidden md:flex gap-12 text-gray-dark flex-wrap">
                {
                    routes.map((route) => (
                        <Link to={route.href} key={route.href} className={`dark:text-gray-light text-3xl ${location === route.href && 'font-bold'}`}>
                            {route.label}
                        </Link>
                    ))
                }
            </nav>
            <nav ref={menuRef}
                className={`absolute top-full left-0 w-full
                            md:hidden
                            transition-all duration-300
                            overflow-hidden
                            px-3
                            ${openMenu ? "max-h-40 opacity-100" : "max-h-0 opacity-0"}`}>
                <div className="flex flex-col mb-2 shadow-[6px_6px_4px_rgba(0,0,0,0.25)]">
                    {
                        routes.map((route) => (
                            <Link onClick={() => setOpenMenu(!openMenu)} to={route.href} key={route.href} className={`border border-gray-dark dark:dark:text-gray-light bg-base-light dark:bg-black text-gray-dark text-base p-2.5 ${location === route.href && 'font-bold'}`}>
                                {route.label}
                            </Link>
                        ))
                    }
                </div>
            </nav>
        </>
    )
}

export default Navbar;